<template>
  <v-row justify="center">
    <v-col cols="12" sm="6" md="2">
      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="date"
        transition="scale-transition"
        offset-y
        min-width="auto"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            v-model="date"
            label="Date karting"
            prepend-icon="mdi-calendar"
            readonly
            v-bind="attrs"
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker v-model="date" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="menu = false"> Cancel </v-btn>
          <v-btn text color="primary" @click="changeDate(date)"> OK </v-btn>
        </v-date-picker>
      </v-menu>
    </v-col>
    <v-col cols="12" sm="12" md="12">
      <v-simple-table fixed-header height="720px">
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-center">Heure</th>
              <th class="text-center" v-for="day in days" :key="day">
                {{ day }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="slot in slots" :key="slot.time">
              <td class="text-center">{{ slot.time }}</td>
              <td class="text-center" v-for="day in days" :key="day">
                <v-btn color="primary" elevation="2" outlined>
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-col>
  </v-row>
</template>
<script>
export default {
  mounted() {
    this.init();
  },
  methods: {
    checkReservation(time, day) {
      return "<v-btn elevation='2'>+</v-btn>";
    },
    changeDate(date) {
      this.$refs.menu.save(date);
      this.days = [];
      this.initDays();
    },
    init() {
      this.initSlots(this.timeOpen, this.timeClose, this.duration);
      this.initDays();
    },
    initDays() {
      var d = new Date(this.date);
      var tab_jour = new Array(
        "Dimanche",
        "Lundi",
        "Mardi",
        "Mercredi",
        "Jeudi",
        "Vendredi",
        "Samedi"
      );
      var placeDay = tab_jour.indexOf(tab_jour[d.getDay()]);

      if (tab_jour[d.getDay()] != "Dimanche") {
        placeDay--;
      }

      d.setDate(d.getDate() - placeDay);
      for (let i = 0; i < 7; i++) {
        this.days.push(tab_jour[d.getDay()] + "-" + d.toLocaleDateString("fr"));
        d.setDate(d.getDate() + 1);
      }
    },
    timeToMin(time) {
      time = time.split(":");
      return parseInt(time[0]) * 60 + parseInt(time[1]);
    },
    minToTime(min) {
      var x = min / 60;

      var h = Math.floor(x);
      if (h < 10) h = "0" + h;
      var m = (x - h) * 60;
      if (m < 10) m = "0" + m;

      return `${h}:${m}`;
    },
    initSlots(open, close, duration) {
      open = this.timeToMin(open);
      close = this.timeToMin(close);
      while (open < close) {
        this.slots.push({
          date: "14/07/2022",
          time: this.minToTime(open),
          reservation: [],
        });
        open += duration;
      }
    },
  },
  data() {
    return {
      date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10),
      menu: false,
      timeOpen: "08:00",
      timeClose: "20:00",
      duration: 15,
      slots: [],
      days: [],
      planning: [
        {
          kart: [
            {
              date: "2016-05-01",
              slots: [
                {
                  id: 1,
                  client: {
                    id: 1,
                    name: "gilles boyer",
                  },
                  formules: [
                    {
                      id: 1,
                      label: "kart 10min +10ans",
                      prix: 28,
                    },
                  ],
                },
              ],
            },
          ],
        },
      ],
    };
  },
};
</script>

